*,
*::before,
*::after {
	box-sizing: border-box;
}

body {
	color: $base-black;
	font-family: $BASE_FONT_FAMILY;
	font-size: 16px;
    font-weight: 300;
    line-height: 1.8;
	-webkit-font-smoothing: antialiased;
	-webkit-overflow-scrolling: touch;
	text-size-adjust: 100%;
	width: 100%;

	@include sp {
        font-size: 14px;
    }
}

a {
	color: inherit;
	font-size: inherit;
	text-decoration: none;
}

img,
svg {
	height: auto;
	max-width: 100%;
}

button,
input[type="submit"] {
	appearance: none;
	background: transparent;
	border: none;
	cursor: pointer;
	padding: 0;
}

ul,
ol,
li {
	list-style-type: none;
}

em,
address {
	font-style: normal;
}

a[href^="tel:"] {
	@include pc {
		cursor: default;
	}
	@include sp {
		text-decoration: underline;
	}
}

picture {
	font-size: 0;
}
